import React, { Component } from 'react';
// import logo from './logo.svg';
import './Article.css';
import reactImage from "../Amir.jpg";
// import customData from '../images.json';
import twoImage from "../Impish-Indri.jpg";
import reactNewSite from "../reactNewSite.png";
import customD from "../imagesM.png";
import img3 from "../img3.png";


class Article extends Component {
  render() {
    return (
      <div>
      <span>the best landing</span>
      <p id='odin'>Лучший сайт визитка, лендинг пейдж, коммерческое предложение, презентация: товаров, услуг, предложений.</p>
      <img src={customD} alt="Alt" title="Title" />

      <p>Лендинг, одностраничники или полноценный сайт “как очередной фейсбук”. Пройти тестовое задание для устройства на “коммунистическую работу” все равно что сдавать экзамен для поступления на космическую станцию в институт инопланетных ученых, но только на пол шишки умнее всех остальных. Тут такие тренды не пройдут. Вместо этого простой сайт по макету из фигмы, сверстанных на простых флексах или гридах, со взаимодействием javascript, без подключенных библиотек: бутстрапа, джейквери нет. Чаще всего подключенная библиотека, даже с забугорным CDN замедляет сайт. Может быть в маленьком городе cdn не очень хорошо работает и по быстренькому сигануть из точки а, в точку б. Библиотека с сайтом не может.
      Зато можно быть уверенным что самый простой способ скомпилировать сайт на фреймворке.</p>
      <h2>Проще простого.</h2>
      <img src={twoImage} alt="#" title="#" />

      <p>Для высоченной простоты, на самом легком используемым большинством. Как на этом реакте. Можно понять почему реакт лидирует среди фреймворков для создания одностраничников. Порог вхождения в реакт низок. У реакта много мануалов, куча информации. Если сравнивать реакт с ангуляром и вью, то ангуляр мудренее. Найти ответ на вопрос по ангуляру, очень сложно. Реакт знаменит, реакт логичен и прост для стандартного понимания. Со вью история куда тяжелее, потому что пишут: “вью прост и понятен”. Забыв указать главную суть. Вью - коммунистический и простому смертному человеку, надеющимся работать на оплачиваемой работе, трудно понять бедный склад мыслей что значит: “завтра светлое будущее”. Инструкцию по вью наверное переводили с Китайского. Если находить достоинства. Каким бы привлекательным не казался вью, фреймворк выбирается для простоты.  Когда сердцу ближе лаконичный, предсказуемый, понятный способ, для скоростного написания, то реакт выглядит куда привлекательнее. И порог вхождения! (html coder, css, js). Все!</p>
      <span  id='dwa'></span>
      <h2>Доступные технологии.</h2>
      <img src={reactNewSite} alt="alt" title="title" />
      <p>Вместо дорогостоящего виндовс, на бесплатном убунту. Причем на бесплатном языке программирования, с бесплатным гитхабом. И javascript исполняется на стороне устройства, поэтому нет необходимости арендовать коммунистический сервер с дорогущим PHP или еще дороже java-Ой. Ведь проще платить только за действительно полезные дела, которые нужны для улучшения жизни.</p>
      <p id='three'></p>
      <p>По быстренькому сверстать сайт и со структурированной разметкой данных, семантическими тегами взаимодействующие с яваскрипт. И в заключении запустить контекстную рекламу.</p>
      <img src={img3} alt="alt" title="title" />

      </div>
    );
  }
}

export default Article;



