import React, { Component } from 'react';
import './Nav.css';

class Nav extends Component {
  render() {
    return (
      <section>
      <a href="#" style={{color: "green", background:"red"}}>theBest</a><a href="#odin" style={{color: "coral", background:"orange"}}>one</a><a href="#dwa" style={{color: "yellow", background:"cyan"}}>two</a><a href="#three" style={{color: "violet", background:"orange"}}>three</a>
      </section>
    );
  }
}

export default Nav;



